body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

div:focus {
  outline: none !important;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

main {
  height: 100%;
  -webkit-flex-grow: 1;
          flex-grow: 1; }

input {
  box-shadow: 0px 0px 41px -22px rgba(0, 0, 0, 0.75);
  font: inherit;
  will-change: all;
  display: -webkit-flex;
  display: flex;
  -webkit-flex: 1 1;
          flex: 1 1;
  background: #fff;
  border-radius: 6px;
  min-height: 32px;
  border: 1px solid transparent;
  font-size: 16px;
  padding: 8px;
  outline: none !important;
  width: calc(100% - 16px);
  margin-bottom: 16px;
  margin: 0 auto; }

.list {
  margin: 0 auto;
  width: 100%;
  height: 100vh; }

.row-margin > *:not(:last-child) {
  margin-right: 12px; }

.column-margin > *:not(:last-child) {
  margin-bottom: 32px; }

.row {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  padding: 16px 0; }
  .row .row-inner {
    box-shadow: 0px 0px 41px -22px rgba(0, 0, 0, 0.75);
    width: 100%;
    background-color: #fff;
    border-radius: 12px;
    margin: 0 auto;
    max-width: 800px;
    padding: 8px; }
    @media screen and (max-width: 426px) {
      .row .row-inner {
        -webkit-flex-direction: column;
                flex-direction: column; } }
    .row .row-inner a {
      transition: all 0.15s ease-in-out;
      display: -webkit-flex;
      display: flex;
      -webkit-align-items: center;
              align-items: center; }
      @media screen and (max-width: 426px) {
        .row .row-inner a {
          -webkit-flex-direction: column;
                  flex-direction: column; } }
      .row .row-inner a:hover {
        background-color: #f3f3f3;
        border-radius: 12px; }
    .row .row-inner > * {
      padding: 16px 8px; }

img {
  padding: 0 8px;
  border-radius: 50%;
  object-fit: cover; }

button {
  transition: all 0.15s ease-in-out;
  background-color: white;
  border: solid 1px lightgray;
  border-radius: 12px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  font-size: 24px;
  padding: 8px 16px; }
  button:hover {
    background-color: lightgray;
    cursor: pointer; }
  button.danger {
    border-color: #ff3b31;
    color: #ff3b31; }
    button.danger:hover {
      background-color: #ff3b31;
      color: white !important; }
  button.normal {
    border-color: #19a4ea;
    color: #19a4ea; }
    button.normal:hover {
      background-color: #19a4ea;
      color: white !important; }

.button-box {
  margin-bottom: 32px; }

h1 {
  margin: 16px 0; }

h3 {
  margin-bottom: 8px; }

.main {
  display: -webkit-flex;
  display: flex; }
  .main .navigation-box,
  .main .main-box {
    padding: 16px; }
  .main .main-box {
    -webkit-flex-grow: 1;
            flex-grow: 1;
    height: 100%;
    min-height: 100vh;
    background-color: #f3f3f3; }
    .main .main-box .main-box-inner {
      max-width: 800px;
      margin: 0 auto;
      min-height: 100vh; }

ul {
  list-style: none;
  padding-left: 0; }
  ul li {
    font-size: 18px;
    margin: 16px 0; }

nav a {
  transition: all 0.15s ease-in-out;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  padding: 8px 16px;
  margin-left: -16px;
  border-radius: 12px; }
  nav a:hover {
    background-color: #f3f3f3; }

@media screen and (max-width: 426px) {
  .mobile-text-center {
    text-align: center; } }

.favourite-button {
  margin-left: 8px; }

#error-gnome {
  border-radius: 0; }

